section.turret {
  min-height: 100vh;
  background: $dark2 url("../img/bg/landscape.png") no-repeat top center / cover;
  padding-top: px2vw(146);
  padding-bottom: px2vw(50);

  .title-block {
    color: $white;
  }

  .showreel {
    display: flex;
    justify-content: space-between;
    width: px2vw(250);
    margin-top: px2vw(25);

    &-image {
      flex: none;
      width: px2vw(139);
      height: px2vw(103);
      @include center-image;
    }

    &-control {
      flex: none;
      margin-left: px2vw(16);
      width: px2vw(99);

      .play {
        width: px2vw(42);
        height: px2vw(42);
        margin-bottom: px2vw(15);
        @include svg-icon;
      }

      p {
        opacity: 0.8;
      }
    }
  }

  .turret-blocks {
    width: px2vw(255);
    margin-top: px2vw(65);
    text-align: center;

    .block {
      align-items: center;
      color: $white;

      .image {
        width: px2vw(255);
        height: px2vw(160);
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }

      .desc {
        margin-top: px2vw(14);
      }
    }
  }
}
