header {
  position: fixed;
  z-index: 5;
  width: 100vw;
  height: px2vw(110);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 px2vw(24);

  .desktop-menu, .header-bg {
    display: none;
  }

  .logo {
    position: relative;
    width: px2vw(50);
    height: px2vw(50);
    @include svg-icon;
  }

  .burger {
    position: relative;
    width: px2vw(36);
    height: px2vw(20);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    div {
      position: relative;
      width: 100%;
      height: px2vw(1.6);
      background-color: $red;
      flex: none;
      transition: all 0.3s ease;

      &:nth-of-type(2) {
        &::after {
          position: absolute;
          display: block;
          content: "";
          width: 100%;
          height: 100%;
          background-color: $red;
          opacity: 0;
          transition: all .3s ease;
        }

        &::after{
          transform: rotate(-90deg);
        }
      }
    }
  }

  &.menu-open {
    .burger {
      div:nth-of-type(1) {
        opacity: 0;
      }

      div:nth-of-type(2) {
        transform: rotate(45deg);
        &::after{
          opacity: 1;
        }
      }

      div:nth-of-type(3) {
        opacity: 0;
      }
    }
  }


  @include pad {
    padding: 0 40px;
    height: 120px;

    .logo {
      width: 60px;
      height: 60px;
    }

    .burger {
      width: 35px;
      height: 20px;
    }
  }
}
