.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: px2vw(90);
  height: px2vw(90);
  border: px2vw(1) solid $red;
  border-radius: 50%;
  font-family: "Blender Pro", sans-serif;
  color: $white;
  font-size: px2vw(14);
  text-decoration: none;

  @include pad {
    width: 96px;
    height: 96px;
    border-width: 1px;
    font-size: 14px;
  }
}