
@mixin title {
  font-family: "Blender Pro", sans-serif;
  margin: 0;

  span {
    color: $red;
  }
}

@mixin text {
  font-family: "Roboto-Regular", sans-serif;
  font-size: px2vw(11);
  line-height: 160%;
  letter-spacing: 0.03em;

  @include pad {
    font-size: 14px;
  }
}

@mixin text-medium {
  font-family: "Roboto-Medium", sans-serif;
  font-size: px2vw(11);
  line-height: 160%;
  letter-spacing: 0.03em;

  @include pad {
    font-size: 14px;
  }
}

h1,
h2,
h3,
h4,
h5 {
  @include title;
}

h1 {
  font-size: px2vw(36);
  line-height: 93%;
  text-transform: uppercase;

  @include pad {
    font-size: 40px;
  }
}

h2 {
  font-size: px2vw(24);
  line-height: 93%;
  text-transform: uppercase;

  @include pad {
    font-size: 30px;
  }
}

p.regular {
  @include text;
}

p.medium {
  @include text-medium;
}