section.facts {
  padding-top: px2vw(62);
  padding-bottom: px2vw(62);
  background-color: $white;

  .title-block {
    h1 {
      color: $dark;
    }

    p {
      margin-top: px2vw(15);
      color: $blue-grey;
    }
  }

  .facts-wrapper {
    margin-top: px2vw(35);
  }

  .facts-blocks {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .block {
      width: px2vw(120);
      margin-bottom: px2vw(40);

      .title {
        color: $red;
        @include title;
        font-size: px2vw(30);
      }
      p {
        color: $dark;
        line-height: 150%;
      }
    }
  }
}
