section.intro {
  min-height: 100vh;
  padding-top: px2vw(130);

  @include pad {
    padding-top: 150px;
    padding-left: 150px;
  }

  .text-block {
    @include pad {
      width: 50vw;
    }

    .stripe {
      @include text-medium;
      display: inline-block;
      background: $red;
      font-weight: bolder;
      font-size: px2vw(8.5);
      padding: px2vw(4) px2vw(9);
      text-transform: uppercase;
      color: $dark2;
      margin-bottom: px2vw(15);
    }

    h1 {
      color: $white;
    }
    p {
      margin-top: px2vw(10);
      margin-bottom: px2vw(30);
      color: $blue-grey;
    }
  }

  .intro-bg {
    position: absolute;
    width: 105%;
    height: 105%;
    top: -1.5%;
    left: -1.5%;
    background: $dark2 url("../img/bg/intro-bg.jpg") no-repeat bottom right 65% /
      cover;
    z-index: -1;
    pointer-events: none;

    @include pad {
      // background-position: left bottom;
    }
  }

  .turret {
    position: absolute;
    width: px2vw(130);
    height: px2vw(97);
    bottom: px2vw(80);
    left: px2vw(-30);
    background: url("../img/bg/turret.png") no-repeat center center / cover;
    z-index: -1;

    @include phonex {
      left: px2vw(-55);
    }

    @include pad {
      left: 45px;
      bottom: 140px;
      width: 170px;
      height: 127px;
    }
  }

  .soldier {
    position: absolute;
    width: px2vw(290);
    height: px2vw(338);

    // Title in 2 rows
    // width: px2vw(270);
    // height: px2vw(315);

    // width: px2vw(250);
    // height: px2vw(292);
    pointer-events: none;
    bottom: 0;
    right: px2vw(35);

    .hud {
      display: none;
    }
    .person {
      position: absolute;
      width: 100%;
      height: 100%;
      bottom: 0;
      left: 0;
      background: url("../img/bg/soldier.png") no-repeat center center / cover;
    }

    @include phonex {
      width: 344px;
      height: 396px;
      right: 20px;
    }

    @include pad {
      width: 500px;
      height: 584px;
      right: 80px;
    }
  }

  .bullet {
    position: absolute;
    width: px2vw(200);
    height: px2vw(46);
    bottom: px2vw(35);
    right: px2vw(140);
    background: url("../img/bg/bullet.png") no-repeat center center / cover;
    mix-blend-mode: screen;
    pointer-events: none;
  }

  .grass {
    position: absolute;
    width: 100%;
    height: px2vw(340);
    bottom: 0;
    right: 0;
    background: url("../img/bg/grass.png") no-repeat center center / cover;
    mix-blend-mode: screen;
    pointer-events: none;
  }
}
