.mobile-menu {
  position: absolute;
  width: 100vw;
  min-height: 100vh;
  top: 0;
  left: 0;
  padding-top: px2vw(130);
  padding-bottom: px2vw(25);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  visibility: hidden;
  background: $dark2 url("../img/bg/landscape.png") bottom center / cover;

  @include pad {
    padding: 150px 0 45px;
  }

  nav {
    text-align: center;

    a {
      display: block;
      @include title;
      font-size: px2vw(30);
      color: $white;
      text-decoration: none;
      margin-bottom: px2vw(20);
      text-transform: uppercase;

      @include pad {
        font-size: 36px;
        margin-bottom: 20px;
      }
    }
  }

  .social-icons {
    display: flex;
    align-items: center;

    a {
      width: px2vw(26);
      height: px2vw(26);
      @include svg_icon;
      margin: 0 px2vw(10);

      svg path {
        fill: #7B7D89;
      }

      @include pad {
        height: 26px;
        width: 26px;
        margin: 0 10px;
      }
    }
  }

  .createdby {
    display: block;
    text-align: center;
    color: $white;

    span {
      opacity: .3;
    }

    a {
      color: $white;
      @include text;
      text-decoration: none;
      opacity: .6;
    }
  }


}
