section.about {
  background: $white;
  padding-top: px2vw(135);
  z-index: 2;

  @include pad {
    padding-top: 140px;
  }

  .image-block {
    p {
      color: $blue-grey;
    }

    .image1 {
      margin-top: px2vw(35);
      width: px2vw(255);
      height: px2vw(169);
      @include center-image;
    }

    .image2 {
      position: absolute;
      width: px2vw(327);
      height: px2vw(192);
      @include center-image;
      left: 50%;
      bottom: px2vw(-48);
      transform: translateX(-50%);
      margin: 0 auto;
    }
  }


}
