footer {
  .social-icons {
    display: none;
  }

  .video-popup {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;

    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background: $dark2;
      opacity: 0.84;
      z-index: -1;
    }

    .video-block {

      video {
        width: 100vw;
        height: auto;
      }

      .close-popup {
        position: absolute;
        top: px2vw(38);
        right: px2vw(25);
        width: px2vw(35);
        height: px2vw(35);
        @include svg-icon;
      }
    }
  }
}
