// Tablets
$screen-pad-min: 700px;
$screen-padpro-min: 1024px;


// Long height phones
@mixin phonex {
    @media (max-width: 400px) and (min-height: 700px) {
        @content;
    }
}

// Tablets
@mixin pad {
    @media (min-width: #{$screen-pad-min}) {
        @content;
    }
}

@mixin padpro {
    @media (min-width: #{$screen-padpro-min}) {
        @content;
    }
}


