section.contacts {
  background: $white;
  padding: px2vw(55) 0 0 0;

  .contacts-header {
    padding-left: px2vw(60);
    // padding-right: px2vw(225);

    &__title {
      margin-bottom: px2vw(20);
    }

    &__info {
      display: flex;
      flex-direction: column;

      .connect {
        order: 2;
        a {
          display: inline-block;
          @include title;
          font-size: px2vw(22);
          font-weight: 700;
          text-decoration: none;
          line-height: 110%;
          transition: 0.3s ease;
        }

        .phone {
          color: $dark;
          &:hover {
            color: $dark2;
          }
        }

        .email {
          color: $red;
          &:hover {
            color: $dark2;
          }
        }
      }

      .address {
        order: 1;
      }
    }

    .copyright {
      display: none;
    }
  }

  .contacts-body {
    margin: px2vw(45) px2vw(25) 0;
    display: flex;
    flex-direction: column-reverse;

    .contacts-image {
      width: 100%;
      height: px2vw(250);
      @include center-image;
    }

    .contacts-map {
      width: 100%;
      height: px2vw(250);
    }
  }

  .contacts-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: px2vw(105);

    .copyright {
      color: $blue-grey;
      @include text;
      opacity: 0.5;
    }

    .createdby {
      @include text;
      color: $blue-grey;

      a {
        @include text;
        text-decoration: none;
        color: $blue-grey;
        opacity: 0.5;
      }
    }
  }
}
