*,
*:focus,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

body {
  display: block;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}
html {
  height: -webkit-fill-available;
}

main {
  overflow: hidden;
}

section {
  position: relative;
  padding-left: px2vw(60);
  padding-right: px2vw(60);
}

.pointer {
  display: none;
}

.error404 {
  .burger {
    display: none;
  }
}
