section.team {
  background: $dark2;
  padding-top: px2vw(70);
  padding-bottom: px2vw(70);

  .title-block {
    color: $white;
  }

  .team-block {
    margin-top: px2vw(55);

    .member {
      margin-bottom: px2vw(40);

      .image {
        width: px2vw(255);
        height: px2vw(255);
        @include center-image;
        margin-bottom: px2vw(10);
      }

      h2 {
        font-size: px2vw(20);
        color: $red;
      }

      p {
        margin-top: px2vw(10);
        margin-bottom: px2vw(5);
        color: $blue-grey;
      }

      a {
        @include text;
        color: $blue-grey;
        text-decoration: none;
      }
    }
  }
}