section.gallery {
  background: $dark2;
  padding-left: 0;
  padding-right: 0;

  .left-slider {
    height: px2vw(250);
    display: flex;
    align-items: center;

    .dark-slider {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 0;

      &__overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: $dark2;
        opacity: 0.85;
      }

      img {
        filter: blur(5px);
      }
    }

    .title-block {
      position: absolute;
      left: px2vw(60);
      width: px2vw(205);
      z-index: 2;

      h2 {
        color: $white;
      }

      p {
        margin-top: px2vw(15);
        color: $blue-grey;
      }
    }

    .slides-nav {
      position: absolute;
      right: px2vw(24);
      width: px2vw(56);
      height: px2vw(128);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      &__button {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: px2vw(54);
        height: px2vw(54);
        border: px2vw(1) solid $blue-grey;
        border-radius: 50%;
        cursor: pointer;

        svg {
          height: px2vw(15);
          width: px2vw(10);
        }
      }

      .next-slide svg {
        transform: rotate(90deg);
      }

      .prev-slide svg {
        transform: rotate(-90deg);
      }
    }
  }

  .right-slider {
    height: px2vw(250);
  }

  .swiper-slide {
    width: 100%;
    height: 100%;
    @include center-image;
  }
}
